
@import './common/css/normalize.css';
@import './common/fonts/iconfont.css';
html, body {
    height: 100%;
    font-size: 14px;
    font-family: 'Microsoft Yahei','Arial';
}
.head-wrap {
    background-color: #438eb9;
    color: #fff;
}
.head-wrap h1 {
    font-size: 28px;
}
.head-wrap h1, .head-wrap p {
    margin: 0;
}
.head-wrap .welcome {
    padding: 12px;
    cursor: pointer;
}
.head-bar {
    position: relative;
    height: 44px;
    width: 90%;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.head-bar .msg {
    position: absolute;
    right: 0;
    top: 44px;
    color: #222 ;
    padding: 0 24px;
    border: 1px solid #999;
    background-color: #fff;
    z-index: 2;
}
.head-bar .msg ul {
    padding: 4px 0 10px;
    border-bottom: 1px #e5e5e5 dashed;
}
.head-bar .msg li, .head-bar .msg p {
    line-height: 26px;
    cursor: pointer;
    font-size: 14px;
}
.head-bar .msg li:hover {
    color: red;
}
.head-bar .msg i {
    margin: 0 6px;
    font-size: 12px;
}
.content-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.content-wrap .tab-link {
    width: 160px;
    padding: 10px;
    border-right: 1px solid #e2e2e2;
}
.tab-link li {
    display: block;
    line-height: 30px;
    color: #666;
    font-size: 14px;
    padding-left: 26px;
    cursor: pointer;
}
.tab-link h3 {
    line-height: 32px;
    margin: 4px 0;
    cursor: pointer;
    color: #333;
    font-size: 14px;
    font-weight: bold;
    border-bottom: 1px solid #e2e2e2;
}
.tab-link h3:hover, .tab-link li:hover {
    background: #acd7f5;
}
.tab-link i {
    margin: 0 6px;
}
.tab-link .active, .tab-link .active:hover {
    background: #75bcec;
    color: #fff;
    font-weight: bold;
}
.content-wrap .content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 10px;
}
table {
    width: 100%;
    border: 1px solid #e4e4e4;
    border-collapse: collapse;
    margin: 0 auto;
}
th, td {
    border: 1px solid #e4e4e4;
    line-height: 36px;
    text-align: center;
}
th {
    background-color: #e7f4ff;
    color:#333;
}
td {
    color:#666666;
}
td.a-link {
    color: #0000ee;
    cursor: pointer;
}
td a {
    color: #45b4ff;
    cursor: pointer;
    display: inline-block;
    padding: 0 6px;
    vertical-align: middle;
}
td.a-link:hover, td a:hover {
    text-decoration: underline;
}
.fade-enter-active {
    transition: all .3s
}
.fade-enter, .fade-leave-active {
    -webkit-transform: translateX(-60px);
            transform: translateX(-60px);
    opacity: 0
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.btn {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}



